.main-content { 
  margin: 0;
}

@media(min-width: 750px) { 
  .main-content {
    margin: 60px;
  }
}

.popcorn-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('https://mir-s3-cdn-cf.behance.net/project_modules/fs/b61dc149628015.58b9ddc680560.gif') repeat;
  z-index: -1;
  animation: animatePopcorn 4.39s linear infinite;
}

@keyframes animatePopcorn {
  0% {
    background-position: 0 0;
  }
  25% {
    background-position: -30px -30px;
  }
  50% {
    background-position: -60px -60px;
  }
  75% {
    background-position: -90px -90px;
  }
  100% {
    background-position: -120px -120px;
  }
}
